<style>
</style>
<template>
  <!--组织机构->部门管理-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
       <span class="page-title">部门管理</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <Input v-model="searchParam" class="grey" placeholder="名称" @on-enter="getDepList(1)" style="width:300px"/>
      <Button @click="getDepList(1)" class="grey" style="margin-left:5px;width:80px;">搜索</Button>
      <Button v-if="editable" class="grey" style="margin-left:5px;width:80px;float: right" @click="openNewDep()" >添加</Button>
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns" border :data="deps" class="auto-height-table grey" style="width:100%">
      </Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="getDepList" class="default" :total="pageTotal" :current="pageNum" :pageSize="pageSize" :show-total="true"></Page>
        </div>
      </div>
    </div>

    <!--添加新部门-->
    <Modal v-model="modal" class="default" :title="title" width="800" :mask-closable="false">
      <Form ref="depFrom":model="dep" style="padding: 5px;" :label-width="100" :rules="depValidate">
        <Form-item prop="depName" label="部门名称：" style="padding:0px;">
          <Input placeholder="部门名称输入"  v-model="dep.depName" class="grey" style="width:70%" />
        </Form-item>
      </Form>
      <div slot="footer">
        <Button v-if="title=='添加新部门' && editable" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="addDep('depFrom')">保存</Button>
        <Button v-if="title=='部门信息修改' && editable" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="updDep('depFrom')">保存</Button>
        <Button class="grey"  style="margin-right: 5px; width: 80px;" @click="modal= false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import server from "../../../config/api";
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    data(){
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        pageSize:20,
        pageNum: undefined,
        organizationId:'',
        dep:{
          depName:'',
        },
        pageTotal: undefined,
        modal:false,
        searchParam:'',
        title:'',
        dept:undefined,
        deps:[],
        pid:'',
        orgId:'',
        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width:50
          },
          {
            title: '部门名称',
            key: 'depName',
            align: 'center'
          },
          {
            title: '操作',
            align: 'center',
            width:120,
            render:(h,params)=>{
                if(this.editable){
                  return h('div', [
                    h('i-button',{
                      props:{
                        type:'text',
                        size:'small'
                      },
                      style:{
                        color:'#a9b2fd'
                      },
                      on:{
                        click:()=>{
                          this.openDep(params.row)
                        }
                      }
                    },'修改'),
                    h('i-button',{
                      props:{
                        type:'text',
                        size:'small'
                      },
                      style:{
                        color:'#f9caca'
                      },
                      on:{
                        click:()=>{
                          this.delDep(params.row.depId)
                        }
                      }
                    },'删除')
                  ]);
                }
            }
          }
        ],

        depValidate:{
          depName: [
            { required: true, message: '姓名不能为空', trigger: 'blur' }
          ],
        }
      };
    },
    computed: {
      editable: function () {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.OrganizationMgmt);
      }
    },
    methods: {
      getDepList:function (pageNum) {
        this.$http.get(server.api.organization.department.getDepList.replace('{orgId}', this.organizationId)+"?depName="+this.searchParam+"&pageSize="+this.pageSize+"&pageNum="+pageNum).then((response)=>{
          this.deps= response.body.data.rows;
          this.pageTotal = response.body.data.totalCount;
          this.pageNum = pageNum;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        }, (error) => {
          this.$Message.error('部门列表获取失败!');
        });
      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      addDep:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.dep.orgId=this.organizationId;
            this.$http.post(server.api.organization.department.addDep,this.dep).then((response)=>{
              this.getDepList(this.pageNum);
              this.modal=false;
              this.$Message.success('添加成功!');
            },(error) => {
              this.$Message.error('添加失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },

      updDep:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.dep.orgId=this.organizationId;
            this.$http.put(server.api.organization.department.updDep,this.dep).then((response)=>{
              this.getDepList(this.pageNum);
              this.modal=false;
              this.$Message.success('修改成功!');
            },(error) => {
              this.$Message.error('修改失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },

      openNewDep:function () {
        this.title="添加新部门";
	      this.dep={};
        this.modal=true;
      },

      delDep:function (depId) {
        if(this.deps.length==1 && this.pageNum !=1){
          this.pageNum=this.pageNum-1;
        }
        this.$http.delete(server.api.organization.department.delDep.replace('{depId}', depId)).then((response)=>{
          this.getDepList(this.pageNum);
          this.$Message.success('部门删除成功!');
        }, (error) => {
          this.$Message.error('部门删除失败!');
        });
      },
      openDep:function (dep) {
        this.title='部门信息修改';
        this.dep=JSON.parse(JSON.stringify(dep));
        this.modal=true;
      },
    },
    mounted(){
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.getDepList(1);
      Vue.nextTick(() => {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
    }

  }
</script>



